<script>
  import { onMount } from "svelte";
  import Hero from "./components/Hero.svelte";
  import Points from "./components/Points.svelte";
  import Faq from "./components/Faq.svelte";
  import Offer from "./components/Offer.svelte";
  import StickyButton from "./components/StickyButton.svelte";
  import { getProductData, getSubscriptionData, visibleEl } from "./utils.js";
  import { viewportWidth } from "./store.js";
  import throttle from "just-throttle";

  //AMD SPLIT MODE
  // async function loadFaq() {
  //   return (await import("./components/Faq.svelte")).default;
  // }
  // let FaqComponent = loadFaq();

  const sku = "BNE800BSSUK";

  let main,
    mainHeader,
    headerTopEl,
    headerNavigationEl,
    headerNavigationElHeight,
    desktopView,
    headerTopOffset = -100,
    stickyHidden = true,
    s_btn,
    machineSubscriptionData = getMachineSubscriptionData(),
    lastY = 0;

  $: scrollY = 0;
  $: scrollDir = scrollDirection(scrollY);

  async function getProduct(sku) {
    let item;
    try {
      item = await getProductData(sku);
    } catch (e) {}
    return item;
  }

  async function getSubscriptionPlan(sku) {
    let item;
    try {
      const data = await getSubscriptionData();
      for (const plan of data.subscriptionProfiles) {
        if (plan.productChoices.indexOf(sku) > -1) {
          item = plan;
        }
      }
    } catch (e) {}
    return item;
  }

  async function getMachineSubscriptionData() {
    const p = await getProduct(sku);
    const s = await getSubscriptionPlan(sku);
    return {
      machine: p,
      plan: s,
    };
  }
  const getHeaderHeight = (el) => {
    let header_h = el.getBoundingClientRect().height,
      banner = false, //document.querySelector("#topDelBan"),
      off;
    if (banner) {
      let y = banner.getBoundingClientRect().y;
      let h = banner.getBoundingClientRect().height;
      off = y >= 0 ? y + h : header_h;
    } else {
      off = header_h;
    }
    return off;
  };

  const scrollHandler = (e) => {
    //Check if scroll is up or down
    updateStickyVisibility();
    //    updateMainHeaderVisibility(scrollDir, mainHeader);
  };

  const updateStickyVisibility = () => {
    let _header = headerTopEl ? headerTopEl : mainHeader;
    console.log(_header);
    //Check the offset for the sticky position
    if (desktopView) {
      //Desktop sticky behaviour
      //Check if HeaderNavigationBar is visible in viewport
      headerTopOffset =
        getHeaderHeight(headerTopEl) + headerTopEl.getBoundingClientRect().top;
      let headerHidden = visibleEl(
        headerNavigationEl,
        headerTopOffset - headerNavigationElHeight
      );
      stickyHidden = scrollY === 0 ? true : headerHidden;
      //if (stickyHidden !== stickyHiddenTemp) stickyHidden = stickyHiddenTemp;
    } else {
      headerTopOffset = getHeaderHeight(_header);
      stickyHidden = visibleEl(s_btn, headerTopOffset);
      updateMainHeaderVisibility();
      //Mobile sticky behaviour
      //Check if button of reference inside page is visible in viewport
    }
  };

  const scrollDirection = (y) => {
    const dy = lastY - y;
    lastY = y;
    if (dy < 0) {
      return "down";
    } else {
      return "up";
    }
  };

  const updateMainHeaderVisibility = (dir) => {
    if (stickyHidden) {
      mainHeader.classList.remove("hid");
    } else {
      if (scrollDir === "down") {
        mainHeader.classList.add("hid");
      } else {
        mainHeader.classList.remove("hid");
      }
    }
  };
  const handleResize = (e) => {
    viewportWidth.update((existing) => window.outerWidth);
    desktopView = window.outerWidth > 767;
    updateStickyVisibility();
  };

  onMount(() => {
    mainHeader = document.getElementById("top");
    headerTopEl = document.querySelector(".Header__top-wrapper");
    headerNavigationEl = document.querySelector(".HeaderNavigationBar");
    headerNavigationElHeight = headerNavigationEl.getBoundingClientRect()
      .height;
    headerTopEl.classList.add("loaded");
    desktopView = window.outerWidth > 767;
    viewportWidth.set(window.outerWidth);
    setTimeout(function () {
      s_btn = document.getElementById("Hero__AddToBagButton");
      updateStickyVisibility();
    }, 100);
  });
</script>

<style type="text/scss">@font-face {
  font-family: "NespressoLucas-light";
  src: url(https://www.nespresso.com/shared_res/agility/commons/fonts/NespressoLucas-Light.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "NespressoLucas-normal";
  src: url(https://www.nespresso.com/shared_res/agility/commons/fonts/NespressoLucas-Medium.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "NespressoLucas-bold";
  src: url(https://www.nespresso.com/shared_res/agility/commons/fonts/NespressoLucas-Bold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

main {
  text-align: center;
  padding: 0;
  margin: 0 auto;
  font-family: "NespressoLucas-normal", Arial, Helvetica, sans-serif !important; }

:global(.restrict) {
  max-width: 66.25em;
  padding: 0 2em;
  margin: 0 auto; }

:global(#top) {
  background-color: black; }

:global(#top),
:global(.topDelBan) {
  z-index: 90; }

:global(#top .HeaderNavigationBar__switch),
:global(#top .Header__top-wrapper.loaded) {
  transition: transform 0.15s ease-out;
  transform: translateY(0px); }

:global(#top.hid .HeaderNavigationBar__switch),
:global(#top.hid .Header__top-wrapper.loaded) {
  transform: translateY(-55px); }

#stickyButton {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
  transition: transform 0.25s ease-out 0.25s; }
  #stickyButton.hid {
    transform: translateY(-70px) !important; }
  #stickyButton.visibletop {
    transform: translateY(0px) !important; }

@media only screen and (min-width: 640px) {
  main {
    max-width: none; } }

@media only screen and (min-width: 768px) {
  #stickyButton {
    transition: transform 0.25s ease-out; }
  :global(#top) :global(.HeaderNavigationBar__switch),
  :global(#top) :global(.Header__top-wrapper.loaded) {
    transition: none;
    transform: none; }
  :global(#top).hid :global(.HeaderNavigationBar__switch),
  :global(#top).hid :global(.Header__top-wrapper.loaded) {
    transform: none; } }</style>

<svelte:window
  bind:scrollY
  on:scroll={throttle(scrollHandler, 300)}
  on:resize={handleResize} />

<div
  id="stickyButton"
  class:hid={stickyHidden}
  class:visibletop={!stickyHidden && scrollDir === 'down' && !desktopView}
  class:visibleoffset={(!stickyHidden && scrollDir === 'up') || (!stickyHidden && desktopView)}
  style="transform: translateY({headerTopOffset}px)">
  <StickyButton />
</div>

<main bind:this={main}>
  <Hero />
  <Points />
  {#await machineSubscriptionData then value}
    <Offer data={value} />
  {/await}
  <!-- {#await FaqComponent then value}
    <svelte:component this={value} />
  {/await} -->
  <Faq />
</main>
